import React, { useEffect } from 'react'
import { MapContainer, useMap, GeoJSON, FeatureGroup } from "react-leaflet";
import "leaflet/dist/leaflet.css";
import * as L from "leaflet";
import UnitedStates from "../data/UnitedStates.json";



const UnitedStatesMap = ({selectedIndex,setMapData,setMapCenter,setMapZoom}) => {

    const FitBoundsExample = () => {
        const map = useMap();
        useEffect(() => {
            if (UnitedStates && selectedIndex===0) {
                setMapData(UnitedStates)
                const geoJSONLayer = L.geoJSON(UnitedStates);
                const geoJSONBounds = geoJSONLayer.getBounds();
                map.fitBounds(geoJSONBounds);
                map.setMaxBounds(geoJSONBounds);
    
                const center = geoJSONBounds.getCenter();
                setMapCenter([center.lat, center.lng]);
    
                const boundsZoom = map.getBoundsZoom(geoJSONBounds);
                setMapZoom(boundsZoom);
            }
        }, [UnitedStates]);
    
        return null;
    };

    
    return (
        <>
            <MapContainer
                style={{
                    width: "100%",
                    height: "70vh",
                    backgroundColor: "white",
                    border: "none",
                    margin: "auto",
                    borderRadius: "10px",
                }}
                attributionControl={false}
                interactive={false}
                zoomControl={false}
                scrollWheelZoom={false}
                zoomDelta={0.25}
                zoomSnap={0}
                dragging={false}
                keyboard={false}
                touchZoom={false}
                doubleClickZoom={false}
                boxZoom={false}
                tap={false}
                crs={L.CRS.EPSG4326}
            >
                <FitBoundsExample />
                <FeatureGroup>
                    <GeoJSON
                        data={UnitedStates.features}
                        style={{
                            fillColor: "#84857e",
                            fillOpacity: 0.5,
                            weight: 1,
                            color: "whitesmoke",
                            interactive: false,
                        }}
                    />
                </FeatureGroup>
            </MapContainer>
        </>
    )
}

export default UnitedStatesMap